import { Injectable } from '@angular/core';
import { io } from 'socket.io-client';
import { Observable } from 'rxjs/Observable';
import * as Rx from 'rxjs/Rx';
// import { environment } from '../environments/environment';

@Injectable({
    'providedIn': 'root'
})
export class WebsocketService {

    // Our socket connection
    private socket;

    constructor() { }

    connect(): Rx.Subject<MessageEvent> {
        // If you aren't familiar with environment variables then
        // you can hard code `environment.ws_url` as `http://localhost:5000`
        // this.socket = io('https://8131-124-109-35-137.in.ngrok.io/')
        this.socket = io('https://onerooms-socket1.herokuapp.com:443');

        // We define our observable which will observe any incoming messages
        // from our socket.io server.
        // const data = {
        //     channel: '',
        //     guest: ''
        // }
        let observable = new Observable(observer => {
            this.socket.on('recieve-message', ({ message, channel, date, senderid, recieverid, pic, attachment_file, messageId, senderNameFirstChar }) => {
                const data = { 
                    message: message, 
                    channel: String(channel), 
                    date: date, 
                    senderid: senderid,
                    recieverid: recieverid, 
                    pic: pic, 
                    attachment_file: attachment_file, 
                    messageId: messageId, 
                    senderNameFirstChar: senderNameFirstChar 
                }
                observer.next(data);
            })
            return () => {
                this.socket.disconnect();
            }
        });

        // We define our Observer which will listen to messages
        // from our other components and send messages back to our
        // socket server whenever the `next()` method is called.
        let observer = {
            next: (data: Object) => {
                if (data['check'] != 'join') {
                    let message = data['message']
                    let channel = String(data['channel']);
                    let date = data['date'];
                    let senderid = data['senderid']
                    let recieverid = data['receiver_id']
                    let pic = data['pic']
                    let attachment_file = data['pic']
                    let messageId = data['messageId']
                    let senderNameFirstChar = data['senderNameFirstChar']
                    this.socket.emit('send', ({ message, channel, date, senderid, recieverid, pic, attachment_file, messageId, senderNameFirstChar }));
                }
                else {
                    let channel = String(data['channel']);
                    let guest = 'guest';
                    this.socket.emit('join', { channel, guest })
                }
            },
        };

        // we return our Rx.Subject which is a combination
        // of both an observer and observable.
        return Rx.Subject.create(observer, observable);
    }

}